.container {
    width: 1110px;
    height: 80%;
    display: flex;
    justify-content: space-between;
    border: 2px solid;
}
/*1111111111111111111111111111111111111111111111111111111111111111111111111*/
.firstBlock{
   background-color: #2f792f;
    height: 100%;
    width: 25%;
}
.photo {
    margin-top: -50px;
    padding: 20px;
    height: 180px;
    width: 180px;
    border-radius: 90%;
}
.inputFile {
    display: none;
}
.customfileupload {
    border: 1px solid #ccc;
    display: inline-block;
    padding: 6px 12px;
    cursor: pointer;
    color: #000000;
}
/*222222222222222222222222222222222222222222222222222222222222222222222222222*/
.secondBlock{
    background-color: #792f43;
    height: 100%;
    width: 30%;
}
/*33333333333333333333333333333333333333333333333333333333333333333333333333333*/
.endBlock{
    background-color: #2f3b79;
    height: 100%;
    width: 30%;
}
